@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700);
body {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;
    font-family: 'Montserrat', serif;
}

#root {
    width: 100%;
    height: 100%;

    margin: 0;
    padding: 0;
    display: table;
    position: absolute;

    background-color: #1F273D;
}

#page-wrapper {
    width: 100%;
    height: 100%;
    display: table-cell;
    vertical-align: middle;
    position: relative;
    margin: 0;
    padding: 0
}
#menu-wrapper {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    max-height: 65px;
    overflow-y: auto;
}

#menu-table {
    width: 80%;
    margin: 0 auto;
    table-layout: fixed;
    text-align: center;
    display: block;
}

.menu-opt:hover {
    background-color: #344166;
}

.menu-opt {
    display: inline-block;
    font-size: 2vmin;

    -webkit-animation-name: underlineflash;

            animation-name: underlineflash;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}

.menu-text {
    padding: 0 15px;
    margin-bottom: 0;
}

@-webkit-keyframes underlineflash {
    0% {
        color: #FFFDFD;
    }
    25% {
        color: #CBD2D0;
    }
    50% {
        color: #FFFDFD;
    }
    75% {
        color: #FFFDFD;
    }
    100% {
        color: #FFFDFD;
    }
}

@keyframes underlineflash {
    0% {
        color: #FFFDFD;
    }
    25% {
        color: #CBD2D0;
    }
    50% {
        color: #FFFDFD;
    }
    75% {
        color: #FFFDFD;
    }
    100% {
        color: #FFFDFD;
    }
}

@media only screen and (max-width: 600px), (orientation: portrait) {
    #menu-table {
        width: 95%;
    }
    .menu-opt {
        font-size: 2.5vmin;
    }
}
#con-wrapper {
    width: 40%;
    margin: 0 auto;

    text-align: center;
}

hr {
    border-color: #FFF;
}

.con-link a, a:visited {
    text-decoration: none;
    color: #FFFDFD;
    font-size: 7vmin;
}

.con-link a:hover {
    color: #CBD2D0;
}

#edu-table {
    width: 60%;
    margin: 0 auto;

    table-layout: fixed;
}

.edu-top-row {
}

.edu-type {
    font-size: 5vmin;
    margin: 0;
    color: #FFFDFD;
}

.edu-name {
    font-size: 3.5vmin;
    margin: 0;

    color: #F0E100;
}

.edu-result {
    font-size: 4.5vmin;
    margin: 0;
    color: #CBD2D0;
}

.edu-cell {
    padding: 0 5px;
}

.edu-cell-bottom {
    vertical-align: top;
}

@media only screen and (max-width: 600px), (orientation: portrait) {
    #edu-table {
        width: 80%;
        margin: 0 auto;

        table-layout: fixed;
    }
}
#emp-wrapper {
    max-height: 70%;
    width: 60%;
    margin: 0 auto;

    overflow-x: hidden;
    overflow-y: auto;

    padding: 20px;
}

#emp-table {
    max-height: 80%;
    width: 90%;
    margin: 0 auto;

    table-layout: fixed;
}

.emp-name {
    font-size: 3em;
    margin: 0;
    color: #fffdfd;
}

.emp-det {
    font-size: 1.5em;
    margin: 0;

    color: #AAA;
}

.emp-desc {
    text-align: justify;
    font-size: 1.1em;
    margin-bottom: 30px;
    margin-top: 0;
    color: #F0E100;
}

@media only screen and (max-width: 600px), (orientation: portrait) {

    .emp-name {
        font-size: 2em;
    }

    .emp-det {
        font-size: 1em;
    }
}
#scroll-box {
    max-height: 70%;
    width: 60%;
    margin: 0 auto;

    overflow-x: hidden;
    overflow-y: auto;

    padding: 20px;
}

.port-item {
    max-height: 300px;
    margin-top: 15px;
    margin-bottom: 15px;
    padding-right: 5px;

    overflow-y: auto;

    border-bottom: 1px solid darkgrey;
}

.port-item img {
    height: 200px;
    float: left;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 10px;
}

.port-item a {
    color: #fffdfd;
    text-decoration: none;
}

.port-title {
    margin: 0;
    color: #fffdfd;
}

.port-desc {
    margin-top: 0;
    text-align: justify;
}

.content {
    width: 100%;
    color: #F0E100;
}

.image-container {
    float: left;
    height: 200px;
}

.content::-webkit-scrollbar {
    display: none;
}

.port-item:nth-child(2n) img {
    margin-left: 10px;
    margin-right: 0;
    float: right;
}

@media screen and (max-width: 600px) {
    .port-item {
        margin-top: 5px;
    }

    .port-item img {
        height: 100px;
    }

    .image-container {
        height: 100px;
    }
}

.name-title {
    font-size: 15vmin;
    text-align: center;

    margin: 0;

    color: #FFFDFD;
}

.role-title {
    font-size: 5vmin;
    text-align: center;

    margin: 0;

    color: #F0E100;
}

.flashing {
    -webkit-animation-name: monoscroll;
            animation-name: monoscroll;
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
}

@-webkit-keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

@keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

