#menu-wrapper {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    max-height: 65px;
    overflow-y: auto;
}

#menu-table {
    width: 80%;
    margin: 0 auto;
    table-layout: fixed;
    text-align: center;
    display: block;
}

.menu-opt:hover {
    background-color: #344166;
}

.menu-opt {
    display: inline-block;
    font-size: 2vmin;

    animation-name: underlineflash;
    animation-timing-function: ease-in-out;
    animation-duration: 2s;
    animation-iteration-count: infinite;
    animation-direction: alternate-reverse;
}

.menu-text {
    padding: 0 15px;
    margin-bottom: 0;
}

@keyframes underlineflash {
    0% {
        color: #FFFDFD;
    }
    25% {
        color: #CBD2D0;
    }
    50% {
        color: #FFFDFD;
    }
    75% {
        color: #FFFDFD;
    }
    100% {
        color: #FFFDFD;
    }
}

@media only screen and (max-width: 600px), (orientation: portrait) {
    #menu-table {
        width: 95%;
    }
    .menu-opt {
        font-size: 2.5vmin;
    }
}