#scroll-box {
    max-height: 70%;
    width: 60%;
    margin: 0 auto;

    overflow-x: hidden;
    overflow-y: auto;

    padding: 20px;
}

.port-item {
    max-height: 300px;
    margin-top: 15px;
    margin-bottom: 15px;
    padding-right: 5px;

    overflow-y: auto;

    border-bottom: 1px solid darkgrey;
}

.port-item img {
    height: 200px;
    float: left;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-right: 10px;
}

.port-item a {
    color: #fffdfd;
    text-decoration: none;
}

.port-title {
    margin: 0;
    color: #fffdfd;
}

.port-desc {
    margin-top: 0;
    text-align: justify;
}

.content {
    width: 100%;
    color: #F0E100;
}

.image-container {
    float: left;
    height: 200px;
}

.content::-webkit-scrollbar {
    display: none;
}

.port-item:nth-child(2n) img {
    margin-left: 10px;
    margin-right: 0;
    float: right;
}

@media screen and (max-width: 600px) {
    .port-item {
        margin-top: 5px;
    }

    .port-item img {
        height: 100px;
    }

    .image-container {
        height: 100px;
    }
}
