.name-title {
    font-size: 15vmin;
    text-align: center;

    margin: 0;

    color: #FFFDFD;
}

.role-title {
    font-size: 5vmin;
    text-align: center;

    margin: 0;

    color: #F0E100;
}

.flashing {
    animation-name: monoscroll;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-direction: alternate-reverse;
    animation-iteration-count: infinite;
}

@keyframes monoscroll {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}
