#emp-wrapper {
    max-height: 70%;
    width: 60%;
    margin: 0 auto;

    overflow-x: hidden;
    overflow-y: auto;

    padding: 20px;
}

#emp-table {
    max-height: 80%;
    width: 90%;
    margin: 0 auto;

    table-layout: fixed;
}

.emp-name {
    font-size: 3em;
    margin: 0;
    color: #fffdfd;
}

.emp-det {
    font-size: 1.5em;
    margin: 0;

    color: #AAA;
}

.emp-desc {
    text-align: justify;
    font-size: 1.1em;
    margin-bottom: 30px;
    margin-top: 0;
    color: #F0E100;
}

@media only screen and (max-width: 600px), (orientation: portrait) {

    .emp-name {
        font-size: 2em;
    }

    .emp-det {
        font-size: 1em;
    }
}