#edu-table {
    width: 60%;
    margin: 0 auto;

    table-layout: fixed;
}

.edu-top-row {
}

.edu-type {
    font-size: 5vmin;
    margin: 0;
    color: #FFFDFD;
}

.edu-name {
    font-size: 3.5vmin;
    margin: 0;

    color: #F0E100;
}

.edu-result {
    font-size: 4.5vmin;
    margin: 0;
    color: #CBD2D0;
}

.edu-cell {
    padding: 0 5px;
}

.edu-cell-bottom {
    vertical-align: top;
}

@media only screen and (max-width: 600px), (orientation: portrait) {
    #edu-table {
        width: 80%;
        margin: 0 auto;

        table-layout: fixed;
    }
}